*{
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Header{
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  height: 100vh;
}
.Container{
  max-width: 1300px;
  margin: auto;
}
.Navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 35px 0;
}
.NavLogo{
  font-weight: 600;
  font-size: 48px;
  color: #FAFAFA;
}
.NavList{
  display: flex;
  align-items: center;
}
.NavLink{
  margin-right: 60px;
  font-weight: 600;
  font-size: 30px;
  color: #EBEBEB;
  transition: .5s;
}
.NavLink:hover{
  color: #FFFFFF;
}
.NavBtn{
  padding: 16px 36px;
  background: linear-gradient(90deg, #345AA4 -28.35%, rgba(63, 0, 156, 0) 153.96%);
  border-radius: 13px;
  font-weight: 500;
  font-size: 24px;
  color: #E5E5E5;
}

.HeaderInfo{
  display: flex;
  align-items: center;
}
.Title{
  font-weight: 600;
  font-size: 59px;
  color: #FFFFFF;
}
.Subtitle{
  font-weight: 500;
  font-size: 28px;
  color: #E5E5E5;
  margin-bottom: 55px;
}
.HeaderBtn{
  padding: 22px 65px;
  background: linear-gradient(90deg, #345AA4 0%, rgba(63, 0, 156, 0.92) 164.26%);
  border-radius: 13px;
  font-weight: 500;
  font-size: 36px;  
  color: #E5E5E5;
}